.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__link {
        width: 251px;
        height: 45px;
    }
    &__img {
        width: 100%;
        height: 100%;
    }
    &__official {
        color: #FFF;
        text-align: center;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 12px; 
    }
    &__firms {
        margin-top: 7px;
        display: block;
        color: #FFF;
        text-align: center;
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
    }
    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
    }
    &__telephone {
        text-decoration: none;
        color: #FFF;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 18px; 
    }
    &__button {
        margin-top: 4px;
    }
}
@media (max-width:767px) {
    .header {
        &__link {
            width: 162px;
            height: 31px;
        }
        &__firms {
            font-size: 13px;
        }
        &__telephone {
            font-size: 15px;
        }
    }
 }

@media (max-width:575px) {
   .header {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    &__official {
        margin-top: 20px;
    }
    &__wrapper {
        margin-top: 20px;
    }
    &__link {
        width: 150px;
        height: 25px;
    }
    &__telephone {
        font-size: 14px;
    }
    &__firms {
        font-size: 13px;
    }
   }
}
