.promo {
    padding-top: 21px;
    padding-bottom: 93px;
    height: 650px;
    background: url("../../img/bg.jpg") center center / cover no-repeat;
   
    &__title {
        margin: 0;
        margin-top: 91px;
        text-align: right;
        color: #FFF;
        font-size: 42px;
        font-style: normal;
        font-weight: 700;
        line-height: 42px; /* 100% */
        text-transform: uppercase;
    }
    &__subtitle {
        margin: 0;
        margin-top: 17px;
        color: #FFF;
        text-align: right;
        font-size: 24px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px; /* 100% */
        text-transform: uppercase;
    }
    &__wrapper {
        margin-top: 60px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-end;
    }
    &__icons {
        display: flex;
        width: 424px;
    }
    &__icon {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        &_size_1 {
            width: 136px;
        }
        &_size_2 {
            width: 105px;
        }
        &_size_3{
            width: 133px;
        }
        &_mr_1 {
            margin-right: 39px;
        }
    }
    &__icon-container-img {
        width: 42px;
        height: 46px;
    }
    &__icon-img {
        width: 100%;
        height: 100%;
    }
    &__icon-text {
        margin-top: 20px;
        color: #FFF;
        text-align: center;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 14px; /* 100% */
    }
    &__catalog {
        margin-top: 10px;
        color: #FFF;
        text-align: right;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
    }
    &__catalog-link {
       
    }
    &__button {
        margin-top: 68px;
    }

}
@media (max-width:991px) {
    .promo {
        &__container{
            padding: 0 20px;
        }
    }
}
@media (max-width:767px) {
   .promo {
        &__title {
            font-size: 30px;
            line-height: 30px;
        }
        &__subtitle {
            font-size: 16px;
            line-height: 16px;
        }
        &__catalog {
            font-size: 15px;
        }

   }
}
@media (max-width:575px) {
    .promo {
         &__title {
            margin-top: 40px;
             font-size: 25px;
             line-height: 30px;
         }
         &__subtitle {
             font-size: 13px;
             line-height: 16px;
         }
         &__icons {
            width: 100%;
         }
         &__icon-text {
            font-size: 10px;
         }

         &__catalog {
             font-size: 12px;
        }
 
    }
}
 