.card {
    width: 303px;
    min-height: 442px;
    //display: flex;
   // flex-direction: column;
    border: 2px solid #E8E8E8;
    padding: 20px 26px;
    overflow: hidden;
    margin-bottom: 20px;
    &__img {
        display: block;
        margin:  0 auto;
        max-width: 100%;
        height: 200px;
    }
    &__title {
        color: #0D0D0D;
        text-align: center;
         font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: normal; 
        margin-top: 15px;
    }
    &__descr {
        margin-top: 15px;
        color: #0D0D0D;
        text-align: center;
        font-size: 14px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
    }
    &__link {
        margin-top: 10px;   
        color: $main-color;
        display: block;
        text-align: center;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 14px; 
        text-decoration-line: underline;
    }
    &__footer {
        margin-top: 17px;
        border-top: 2px solid #E8E8E8;
        display: flex;
        justify-content: space-between;
    }
    &__prices {
       margin-top: 24px;
    }
    &__price-old {
        color: #555;
        font-size: 14px;
        font-style: normal;
        font-weight: 300;
        line-height: 14px;
        text-decoration: line-through;
    }
    &__price {
        margin-top: 2px;
        color: #050505;
        font-size: 20px;
        font-style: normal;
        font-weight: 700;
        line-height: 20px; 
    }
    &__button {
        margin-top: 17px;
    }
    &__wrapper {
        display: flex;
        width: calc(200% + 40px);
    }
    &__content {
        width: 50%;
        margin-right: 20px;
        transform: translateX(-100%);
        margin-right: 0;
        transition: 0.5s  all;
        &_active {
            transform: translateX(0%);
            margin-right: 20px;
        }
    }
    &__list {
        position: relative;
        width: 50%;
        margin-left: 20px;
        transform: translateX(0%);
        padding-right: 20px;
        transition: 0.5s all;
        padding-left:  13px;
        margin-bottom: 0;
        list-style-type: none;
        &_active {
            transform: translateX(-100%);
            margin-left: 0px;
        }
    }
    &__item {
        color: #555;
        font-size: 14px;
        font-style: normal;
        font-weight: 300;
        line-height: 21px; /* 150% */
        margin-bottom: 3px;
        position: relative;
        &:before {
            content: "";
            display: block;
            width: 7px;
            height: 7px;
            background-color: $main-color;
            position:absolute;
            left: -16px;
            top:7px;
        }
    }
    &__back {
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform:  translateX(calc(-50% - 13px));
        color: $main-color;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 14px; 
        text-decoration-line: underline;
        cursor: pointer;
        &:hover {
            color:  $main-color;
        }
    }
}
@media(max-width:991px) {
    .card {
       margin-right: 5px;
    }
}
