.consultation {
    height: 680px;
    padding: 100px 0  108px 0;
    background: url("../../img/bg2.jpg") center center / cover no-repeat;
    &__title {
        color: #FFF;
        font-size: 36px;
        font-style: normal;
        font-weight: 700;
        line-height: 54px; /* 150% */
        text-transform: uppercase;
        text-align: left;
    }
    &__subtitle  {
        color: #FFF;
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: 30px; /* 150% */
    }
    .error {
        border: 1px solid red;
    }
    label.error {
        color: #FFF;
        border: none;
        margin: 5px auto;
    }
}
@media (max-width:991px) {
    .consultation {
        &__container {
            padding: 0 20px;
        }
        &__title {
            font-size: 30px;
        } 

    }
 }
@media (max-width:767px) {
    .consultation{
        padding-top: 50px;
        &__title {
            font-size: 23px;
            line-height: 31px;
         }
    }
     
  }
@media (max-width:575px) {
    .consultation{
        padding-top: 80px;
        &__title {
            font-size: 12px;
            line-height: 20px;
         }
         &__subtitle  {
            margin-top: 20px;
           font-size: 12px;
           line-height: 15px;
        }
    }
}
  