.advantage {
    background-color: #F6F6F6;
    padding-top: 73px;
    padding-bottom: 65px;
    
    &__title {
        color: #0D0D0D;
        text-align: center;
        font-size: 30px;
        font-style: normal;
        font-weight: 700;
        line-height: 30px; /* 100% */
    }
    &__wrapper {
        margin-top: 60px;
    }
}
@media (max-width:991px) {
    .advantage {
        &__container {
            padding:  0 20px;
        }
    }
}
@media (max-width:767px) {
    .advantage {
       &__title {
        font-size: 25px;
       }
       &__container {
         padding:   0 10px;
       }
    }
}
@media (max-width:575px) {
    .advantage {
       &.promo__wrapper {
        margin-top: 30px;
       }
    }
}
