.feed-form {
    display: flex;
    flex-direction: column;
    &__consultation {
        margin-top: 36px;
    }
    &_size_1 {
        width: 300px;
    }
    &__input {
        height: 54px;
        padding: 20px;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        border: none;
        outline: none;
        border-radius: 4px;
        &_mt_11 {
            margin-top: 11px;
        }
    }
}
@media (max-width:575px) {
    .feed-form {
        width: 260px;
        &__input {
            padding: 10px;
            height: 40px;
        }
    }
}