.list-advantage {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    &__item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        &_mr_1 {
            margin-right: 68px;
        }
        &_size_1 {
            width: 269px;
        }
        &_size_2 {
            width: 273px;
        }
        &_size_3 {
            width: 277px;
        }
    }
    &__img {
        &_animate {
            animation: heartbeat 2s ease  infinite;
            &:hover {
                animation-play-state: paused;
            }
        }
    }
    &__title {
        margin-top: 30px;
        color: #0D0D0D;
        text-align: center;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
    }
    &__desc {
        margin-top: 20px;
        color: #0D0D0D;
        text-align: center;
        font-size: 14px;
        font-style: normal;
        font-weight: 300;
        line-height: 18px; 
    }
    &__running {
        color: #F00;
        font-size: 14px;
        font-style: normal;
        font-weight: 300;
        text-decoration-line: underline;
    }
}
@media (max-width:991px) {
   .list-advantage {
        &__item {
            &_mr_1 {
                margin-right: 12px;
            }
        }
        &__desc {
            font-size: 13px;
        }
   }
}
@media (max-width:767px) {
    .list-advantage {
        &__none {
            display: none;
        }
        &__title {
            font-size: 15px;
        }
        &__running {
           font-size: 13px;
        }
        &__desc {
            font-size: 12px;
        }
    }
 }
 @media (max-width:575px) {
    .list-advantage {
       flex-direction: column;
       justify-content: center;
       align-items: center;
       &_mr_1 {
        margin-right: 0;
       }
       &_mt {
            margin-top: 20px;
       }
    }
 }
 