.catalog {
    padding-bottom:  85px;
    &__title {
        color: #0D0D0D;
        text-align: center;
        font-size: 30px;
        font-style: normal;
        font-weight: 400;
        text-transform: uppercase;
        margin: 0;
    }
    &__tabs {
        margin-top: 46px;
        list-style-type: none;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 0;
        padding-left: 0;
        
    }
    &__tab {
        width: 223px;
        height: 52px;
        border: 2px solid #d0d0d0;
        color: #6B6B6B;
        text-align: center;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 52px; 
        margin: 0 4.5px;
        text-transform: uppercase;
        transform: skewX(-25deg);
        cursor: pointer;
        transition:  0.3s all;
        &_active {
            color: #FFF;
            background-color: $main-color;
            border-width: 1px;
        }
        &:hover {
            box-shadow:  3px 3px  20px $main-color;
            border: none;
        }
    }
    &__no-skew {
        transform: skew(25deg);
    }
   
    &__content {
        margin-top: 60px;
        justify-content: space-between;
        flex-wrap: wrap;
        display: none;
        &_active {
            display: flex;
        }
    }
}

@media(max-width:991px) {
    .catalog {
        &__content {
            justify-content: space-evenly;
        }
    }

}
@media(max-width:575px) {
    .catalog {
        &__title {
            font-size: 25px;
        }
        &__tab {
            font-size: 10px;
            height: 40px;
            line-height: 40px;
        }
    }
}