.modal {
    display: none;
    &_active {
        display: block;
    }
    &__overlay {
        position: absolute;
    }
    &_center {
        top:50%;
        left:50%;
        transform: translate(-50%, -50%); 
   }
   &__content {
       background-color: #F2F2F2;
       padding: 40px 28px;
       &_size {
            width: 382px;
            min-height: 402px;
       }
       &_size_1 {
        width: 382px;
       }
    }
    &__close {
        position: absolute;
        cursor: pointer;
        &_style {
            color: $main-color;
        }
        &_size_1 {
            line-height: 21px;
            width: 21px;
            height: 21px;
            font-size:36px
        }
        &_right {
            top:0;
            left:calc(100% + 19px);
        }
    }
    &__header {
        &_none {
            display: none;
        }
    }
    &__title {
        color: #0D0D0D;
        text-align: center;
        font-size: 19px;
        font-style: normal;
        font-weight: 700;
        line-height: 19px; 
        &_none {
            display: none;
        }
    }
    &__desc {
        margin-top: 10px;
        color: #0D0D0D;
        text-align: center;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 14px; 
        &_none {
            display: none;
        }
    }
    &__body {
        margin-top: 26px;
        padding: 0  12px;
        &_none {
            display: none;
        }
    }
    &__footer {
        &_none {
            display: none;
        }
    }
    .error {
        border: 1px solid red;
    }
    label.error {
        border: none;
        margin: 5px auto;
    }
}