.slider {
    width: 750px;
    margin:  0 auto;
    position: relative;
    &__prev, &__next {
        position:absolute;
        border:none;
        background: #fff;
        top:50%;
        z-index: 1000;
        transform: translateY(-50%);
    }
    &__next {
        right: -66px;
    }
    &__prev {
        left: -66px;
    }
    &__arrow {
        height: 50px;
        width: 30px ;
    }
}
@media (max-width:991px) {
    .slider {
        width: 600px;
        &__img {
            width: 100%;
        }
        &__prev {
            left: -50px;
        }
        &__next {
            right: -50px;
        }
    }   
       
}
@media (max-width:767px) {
    .slider {
        width: 100%;
        &__img {
            width: 100%;
        }
       
    }   
       
}
@media (max-width: 567px) {
    .slider {
        width: 100%;
        &__img {
            width: 100%;
        }
       
    }   
       
}