.comments {
    &__container {
        padding:  0 10px;
    }
    &__title {
        color: #0D0D0D;
        text-align: center;
        font-size: 30px;
        font-style: normal;
        font-weight: 700;
        line-height: 30px; 
        text-transform: uppercase;
    }
    &__wrapper {
        margin-top: 60px;
    }
}
@media (max-width:567px) {
    .comments {
        &__title {
            font-size: 20px;
        }
    }
   
}