.button {
    background-color:$main-color;
    border: none;
    outline: none;
    border-radius: 4px;
    cursor: pointer;
    &_style {
        color: #FFF;
        text-align: center;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;
    }
    &_style_fz_12 {
        font-size: 12px;
    }
    &_style_fz_16 {
        font-size: 16px;
    }
    &_style_fz_14 {
        font-size: 14px;
    }
    &_pd_sml {
        padding: 6px 8px 7px 5px;
    }
    &_pd_lg {
        padding: 12px 28px;
    }
    &_size_mini {
        width: 135px;
        height: 44px;
        border-radius: 4px;
    }
    &_size_sml {
        width: 153px;
        height: 32px;
    }
    &_size_lg {
        width: 300px;
        height: 58px;
    }
    &__consultation {
        margin-top: 17px;
    }


}
@media (max-width:767px) {
    .button {
        &_size_sml {
            width: 138px;
            height: 26px;
        }
        &_style_fz_12 {
            font-size: 11px;
        }
        &_size_lg {
            width: 250px;
            height: 36px;
        }
        &_style_fz_16 {
            font-size: 13px;
        }
    }
    
 }
 @media (max-width:575px) {
    .button {
        &_size_sml {
            width: 138px;
            height: 26px;
        }
        &_style_fz_12 {
            font-size: 11px;
        }
        &_size_lg {
            width: 168px;
            height: 35px;
        }
        &_style_fz_16 {
            font-size: 11px;
        }
        &_pd_lg {
            padding: 11px 10px;
        }
    }
    
 }