.footer {
    height: 630px;
    &__wrapper {
        position: relative;
    }
    &__info {
        width: 460px;
        border-radius: 8px;
        height: 270px;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        background-color: $main-color;
    }
    &__adress {
        margin: 0;
        margin-top: 50px;
        text-align: center;
        color: #FFF;
        text-align: center;
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: 20px; /* 100% */
    }
    &__telephone {
        margin-top: 36px;
        display: block;
        text-align: center;
        color: #FFF;
        text-align: center;
        font-size: 20px;
        font-style: normal;
        font-weight: 700;
        line-height: 20px; /* 100% */
        text-decoration: none;
        &:hover {
            color: #FFF;
        }
    }
    &__social {
        margin: 0 auto;
        width: 212px;
        margin-top: 36px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__social-item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 53px;
        height: 52px;
        border-radius: 100%;
        border: 2px solid white;
    }
    &__img {
       height: 25px; 
    }
}
@media (max-width:1170px) {
    .footer {
        &__info {
            left: 100%;
            transform: translateX(-100%);
            width: 370px;
            height: 228px;
        }
        &__adress {
            margin-top: 35px;
        }
    }
}
@media (max-width:724px) {
    .footer {
        &__info {
            left: 100%;
            transform: translateX(-100%);
            width: 275px;
            height: 180px;
        }
        &__adress {
            font-size: 16px;
            margin-top: 20px;
        }
        &__telephone {
            margin-top: 20px;
           font-size: 15px;
        }
        &__social-item {
            width: 40px;
            height: 40px;
        }
    }
}
@media (max-width:619px) {
    .footer {
        &__info {
            left: 275px;
            top: 62%;
            width: 275px;
            height: 180px;
        }
     
    }
}
@media (max-width:400px) {
    .footer {
        &__info {
            left: 240px;
            width: 240px;
            height: 149px;
        }
        &__adress {
            font-size: 16px;
            margin-top: 20px;
        }
        &__telephone {
            margin-top: 5px;
           font-size: 15px;
        }
        &__social-item {
             width: 40px;
            height: 40px;
        }
        &__social {
            margin-top: 10px;
        }
    }
}
