.comment {
    display: flex;
    &_mt {
        margin-top: 60px;
    }
    &__container-img {
        width: 169px;
        height: 169px;
    }
    &__img {
        width: 100%;
        height: 100%;
    }
    &__wrapper {
        display: flex;
        flex-direction: column;
        margin-left: 61px;
    }
    &__name {
        color: #0D0D0D;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 18px; /* 100% */
    }
    &__marafon {
        color: #646464;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
    }
    &__text {
        margin-top: 10px;
        color: #0D0D0D;
        font-size: 16px;
        font-style: normal;
        font-weight: 300;
        line-height: 24px; /* 150% */
    }
}
@media (max-width:899px) {
    .comment {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        &__name {
            text-align: center;
            margin-top: 20px;
        }
        &__marafon {
            text-align: center;
        }
         &__wrapper {
            margin-left: 0px;
        }
      
    }
}
@media (max-width:767px) {
    .comment {
         &__wrapper {
            display: flex;
            flex-direction: column;
            margin-left: 40px;
        }
    }
}
@media (max-width:567px) {
    .comment {
       &__wrapper {
            display: flex;
            flex-direction: column;
            margin-left: 40px;
        }
        &__text {
           font-size: 14px;
           line-height: 16px;
            
        }
    }
}